.Toolbar {
  height: 56px;
  width: 100%;
  /* position: fixed;
  top: 0;
  left: 0; */
  background-color: rgb(148, 189, 152);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  box-sizing: border-box;
  z-index: 90;
}

.Toolbar nav {
  height: 100%;
}


@media (max-width: 699px) {
  .DesktopOnly {
      display: none;
  }
}